
import { defineComponent, ref, watch } from 'vue';
import { QuickData, QuickFetch } from '@/quick';
import { useRoute } from 'vue-router';

export default defineComponent({
  setup() {
    const qf = new QuickFetch();
    const route = useRoute();
    const gameData: any = ref({});
    function getGame() {
      qf.Url(`games/${route.params.gameId}`).GetAll(gameData);
    }
    getGame();
    function exportCsv(data: any, title: string, header: any) {
      let temp = '';
      temp += `${header.join(',')}\r\n`;
      for (let i = 0; i < data.length; i++) {
        temp += `${data[i].join(',')}\r\n`;
      }
      const hiddenElement = document.createElement('a');
      hiddenElement.href = `data:text/csv;charset=utf-8,${encodeURI(temp)}`;
      hiddenElement.target = '_blank';
      hiddenElement.download = `${title}.csv`;
      hiddenElement.click();
    }
    function downloadInsurence() {
      qf.Url(`register/list/${route.params.gameCode}/${route.params.gameId}`).Get().then((res: any) => {
        const list: any = [];
        res.forEach((r: any) => {
          const temp = [];
          temp.push(r.unified_id, r.name, r.birthday, r.org_ch_full, r.dept_ch, r.student_id);
          list.push(temp);
        });
        exportCsv(list, `${gameData.value.name_ch}_名冊`, ['身分證字號', '姓名', '生日', '單位全名', '系所', '學號']);
      });
    }
    function downloadIndividual() {
      qf.Url(`register/ind/list/${route.params.gameCode}/${route.params.gameId}`).Get().then((res: any) => {
        const list: any = [];
        res.forEach((r: any) => {
          const temp = [];
          temp.push(r.ch, r.event_ch, r.org_ch_full, r.dept_ch, r.name, r.student_id);
          list.push(temp);
        });
        exportCsv(list, `${gameData.value.name_ch}_個人項目名冊`, ['項目', '組別', '單位', '系所', '姓名', '學號']);
      });
    }
    function downloadGroup() {
      qf.Url(`register/grp/list/${route.params.gameCode}/${route.params.gameId}`).Get().then((res: any) => {
        const list: any = [];
        res.forEach((r: any) => {
          if (r.member_data_list.length > 0) {
            r.member_data_list.forEach((a: any) => {
              const temp = [];
              temp.push(r.ch, r.event_ch, a.org_ch_full, a.dept_ch, r.team_name, a.name, a.student_id);
              list.push(temp);
            });
          }
        });
        exportCsv(list, `${gameData.value.name_ch}_團體項目名冊`, ['項目', '組別', '單位', '系所', '隊名', '姓名', '學號']);
      });
    }
    return {
      downloadInsurence,
      downloadIndividual,
      downloadGroup,
    };
  },
});
